.Ranking {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow: hidden;

    .level-wrap {
        width: 300px;
        text-align: center;
        color: #bb080a;
        padding: 15px 20px 50px;
        margin-top: 30px;
        margin-bottom: -20px;
    }

    .level-1 {
        background: url(/static/images/levelWrap1.png) no-repeat;
        background-size: 100% 100%;
    }

    .level-2 {
        background: url(/static/images/levelWrap2.png) no-repeat;
        background-size: 100% 100%;
    }

    .level-3 {
        background: url(/static/images/levelWrap3.png) no-repeat;
        background-size: 100% 100%;
    }

    button {
        margin: 10px;
    }

    .button-bar {
        width: 100%;
        display: flex;
        justify-content: center;
        position: absolute;
        bottom: 10%;
    }

    .text-wrap-2 {
        position: relative;
    }

    .text-info {
        font-size: 18px;
        margin: 10px;

        ul {
            margin-top: 5px;
        }
    }

    .doctor-sm {
        position: absolute;
        top: -100px;
        left: 60px;
    }
}