.Progress {
    width: 300px;
    height: 10px;
    background-color: #000;
    border: 3px solid red;
    border-radius:  10px 10px;
    overflow: hidden;

    &-content {
        height: 100%;
        width: 0;
        background-color: #f3af00;
        transition: width 0.5s;
        border-radius: 10px 10px;
    }
}