.Login {
    height: 100%;
    width: 100%;
    background: url(/static/images/background-1.jpg) no-repeat;
    background-size: 100% 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow: hidden;

    .input-line {
        margin: 30px 0 15px;
        display: flex;
        justify-content: center;
        line-height: 40px;

        input {
            font-size: 20px;
            height: 40px;
            padding: 0 10px;
            border: none;
            width: 60%;
        }
        label {
            font-family: 'huaKang';
            font-size: 25px;
            color: #412210;
        }
    }

    .button-md {
        position: absolute;
        bottom: 20%;
    }
}