}
button {
    font-family: 'huaKang';
    border: none;
    outline: none;
    color: #fff;
    text-shadow: 3px 3px 0 #982d35;
}
.button-1 {
    background: url(/static/images/button1.png) no-repeat;
    background-size: 100% 100%;
}
.button-2 {
    background: url(/static/images/button2.png) no-repeat;
    background-size: 100% 100%;
}
.button-3 {
    background: url(/static/images/button3.png) no-repeat;
    background-size: 100% 100%;
    text-shadow: none;
}
.button-4 {
    background: url(/static/images/button4.png) no-repeat;
    background-size: 100% 100%;
    text-shadow: none;
}
.button-lg {
    font-size: 40px;
    padding: 15px 35px;
    margin: 15px 0;
    letter-spacing: 15px;
}
.button-lg:active {
    padding: 18px 38px;
}
.button-md {
    font-size: 35px;
    padding: 10px 30px;
    letter-spacing: 10px;
}
.button-md:active {
    padding: 13px 38px;
}
.button-sm {
    font-size: 28px;
    padding: 8px 18px;
    letter-spacing: 0px;
}
.button-sm:active {
    padding: 10px 20px;
}
.true {
    width: 20px;
    height: 20px;
    line-height: 20px;
    border-radius: 50%;
    background-color: green;
    vertical-align: middle;
}
.false {
    width: 20px;
    height: 20px;
    line-height: 20px;
    border-radius: 50%;
    background-color: red;
    vertical-align: middle;
    color: #fff;
    font-weight: 600;
}
.footer-bar {
    width: 100%;
    height: 30px;
    background: url(/static/images/footer.png) no-repeat;
    background-size: 100% 100%;
    position: absolute;
    bottom: 10px;
    color: #fff;
    font-weight: 600;
}
.text-wrap-1 {
    padding: 30px 45px;
    width: 255px;
    height: 310px;
    background: url(/static/images/text-wrap.png) no-repeat;
    background-size: 100% 100%;
    margin-bottom: 20px;
}
.text-wrap-2 {
    padding: 30px 45px;
    width: 255px;
    height: 310px;
    background: url(/static/images/text-wrap2.png) no-repeat;
    background-size: 100% 100%;
    margin-bottom: 20px;
    margin-top: 20px;
}
.text-list {
    font-family: "fangZheng";
    font-size: 22px;
    width: 185px;
    font-weight: 600;
    
    li {
        margin: 3px;
        line-height: 30px;
    }
    li:first-child {
        margin-top: -10;
    }
    li:last-child {
        width: 150px;
    }
}
.text-list-2 {
    font-size: 18px;
    width: 230px;
    list-style: none;
    padding-left: 20px;

    li {
        padding-bottom: 8px;

        span {
            display: inline-block;
            width: 45px;
            overflow: hidden;
            vertical-align: middle;
            line-height: 22px;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }
}
