.ClassRoom {
    height: 100%;
    width: 100%;
    background: url(/images/background-3.jpg) no-repeat;
    background-size: 100% 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow: hidden;

    .max-wrap {
        width: 100%;
        height: 100%;
        position: fixed;
        z-index: 1;

        .max {
            position: absolute;
            left: 100%;
            transform: rotate(90deg);
            transform-origin: 0 0;
        }
    }

    button {
        position: absolute;
        bottom: 80px;

        .time-count {
            font-family: none;
            font-size: 20px;
            line-height: 50px;
            letter-spacing: 5px;
        }
    }

    .course {
        position: relative;

        .course-content {
            width: 360px;
            height: 230px;
            margin: 40px 0 5px;
        }

        .course-content-mask {
            width: 100%;
            height: 230px;
            position: absolute;
            top: 40px;
        }
    }

    .course-history {
        width: 100%;
        display: flex;
        justify-content: space-between;
        padding: 0;

        li {
            text-align: center;
        }

        img {
            width: 90%;
        }

        .course-mask {
            position: relative;
            bottom: 100%;
            width: 100%;
            height: 100%;
        }
    }
}