.Pharmacy {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow: hidden;

    .text-wrap-2 {
        position: relative;
    }

    .p-1,.p-2,.p-3,.p-4 {
        position: absolute;
        bottom: 25%;
        left: 10%;
    }

    .p-1m,.p-2m,.p-3m,.p-4m {
        position: absolute;
        bottom: 10px;
        right: 0px;
    }

    .button-2 {
        position: absolute;
        bottom: 10%;
    }

    .radio {
        margin: 5px 10px;
        padding: 5px 55px;
    }

    .radio:active {
        padding: 7px 57px;
    }

    .button-box {
        position: absolute;
        bottom: 20%;
        z-index: 1;
    }

    .text-wrap-2 {
        font-size: 20px;
        font-weight: 500;
        letter-spacing: 5px;
    }

    p {
        text-indent: 2em;
        padding: 0 15px 0 10px;
        margin-bottom: 5px;
    }

    ul {
        list-style: none;
        padding: 20px;
        margin: 0px;

        li {
            margin-bottom: 10px;
        }
    }

    .doctor-lg {
        position: absolute;
        bottom: 37%;
        right: 5%;
    }

    .doctor-sm {
        width: 100px;
        height: 150px;
        position: absolute;
        right: 0;
        bottom: -60px;
    }
}