.Loading {
    width: 100%;
    height: 100%;
    background: url(/static/images/background-2.jpg) no-repeat;
    background-size: 100% 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow: hidden;

    &-title {
        font-family: 'huaKang';
        font-size: 20px;
        color: red;
        position: absolute;
        bottom: 75px;
        right: 30px;
        animation-duration: 2s;
    }

    .Progress {
        position: absolute;
        bottom: 55px;
    }
}