.Success {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow: hidden;

    &-1 {
        background: url(/static/images/success-1.jpg) no-repeat;
        background-size: 100% 100%;
    }

    &-2 {
        background: url(/static/images/success-2.jpg) no-repeat;
        background-size: 100% 100%;
    }

    &-3 {
        background: url(/static/images/success-3.jpg) no-repeat;
        background-size: 100% 100%;
    }

    .score {
        font-family: 'huaKang';
        font-size: 50px;
        color: #bb080a;
        transform: rotate(-15deg);
        position: absolute;
        bottom: 30%;
        left: 20%;
        letter-spacing: -8px;
    }
    .doctor-lg {
        position: absolute;
        bottom: 25%;
        left: 50%;
    }
    .handel {
        position: absolute;
        bottom: 80px;
    }
}